import { Button, Space } from 'antd';
import {clearOrderEdit, remove} from "../../store/actions/orders";

export function orderColumns (dispatch, navigate, dishesList) {
  function onDeleteBtnClick (order) {
    dispatch(remove(order.id))
  }

  function onEditBtnClick (order) {
    navigate(`order/${order.id}/edit`)
    dispatch(clearOrderEdit())
  }

  return [
    {
      title: 'Id',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: 'Waiter',
      dataIndex: 'waiterId',
      key: 'waiterId',
      render: (_, record) => record.waiter.firstName
    },
    {
      title: 'Table',
      dataIndex: 'tableId',
      key: 'tableId',
      render: (_, record) => record.table.number
    },

    {
      title: 'Dish',
      dataIndex: 'dishes',
      key: 'dishes',
      render: (dishes) =>
          dishes.map((dish) => {
            const dishData = dishesList.find((item) => item.id === dish.dishId);
            return dishData ? (
                <div key={dish.id}>
                  <span> ({dish.count}) - {dishData.name}</span>
                </div>
            ) : ('');
          })
    },
    {
      title: 'For Payment',
      dataIndex: 'dishes',
      key: 'totalPrice',
      render: (dishes) => {
        const totalPrice = dishes.reduce((accumulator, dish) => {
          const dishData = dishesList.find((item) => item.id === dish.dishId);
          return accumulator + (dishData ? dishData.price * dish.count : 0);
        }, 0);
        return `$${totalPrice}`
      },
    },
    {
      title: 'Actions',
      key: 'actions',
      render: (_, order) => (
        <Space wrap>
          <Button onClick={() => onEditBtnClick(order)} type="primary">Edit</Button>
          <Button onClick={() => onDeleteBtnClick(order)} type="primary" danger>Delete</Button>
        </Space>
      )
    },
  ]
}